import { useState } from 'react';

import { StyledSubheader, StyledSubheader3, StyledSubheader4 } from '../styled';

function CharacterProfile(props) {
  const [expand, setExpand] = useState(false);
  const { name, title, src, brief, emblem, description, stats, logistics, weapons, rules } = props.character;
  const statsArr = stats ? Object.entries(stats) : [];
  const logsArr = logistics ? Object.entries(logistics) : [];
  const weaponsArr = weapons ? Object.entries(weapons) : [];
  const rulesArr = rules ? Object.entries(rules) : [];
  return (
    <section
      className="character-section"
      onClick={() => setExpand(!expand)}
    >
      <section className="char-card">
        <aside>
          <img src={src} alt={name}></img>
        </aside>
        <article className={expand ? "char-summary" : "char-summary compress"}>
          <img className="background-emblem" src={emblem} alt="" />
          <StyledSubheader>{name}</StyledSubheader>
          <StyledSubheader4>{title}</StyledSubheader4>
          <p>{brief}</p>
        </article>
      </section>

      <section className={expand ? "char-stats expand" : "char-stats"}>
        {description?.map(function (paragraph, index) {
          return (
            <p key={`desc-${index}`} className="char-desc expand">{paragraph}</p>
          )
        })}

        <section className={stats ? "campaign-info" : "campaign-info hidden"}>
          <StyledSubheader3>Campaign Information</StyledSubheader3>
          <ul>
            {statsArr.map(function ([key, value], index) {
              return (
                <li key={`stats-${index}`}>
                  <span>{key}: {value}</span>
                </li>
              )
            })}
          </ul>
        </section>

        <section className={logistics ? "campaign-info" : "campaign-info hidden"}>
          <StyledSubheader3>Logistics</StyledSubheader3>
          <table className="stats-table">
            <tr>
              {logsArr.map(function ([key, value], index) {
                return (
                  <th>{key}</th>
                );
              })}
            </tr>
            
            <tr>
              {logsArr.map(function ([key, value], index) {
                return (
                  <td>{value}</td>
                );
              })}
            </tr>
          </table>

          <table className={weapons ? "weapons-table" : "weapons-table hidden"}>
            <tr>
              <th>Weapon</th>
              <th>Range</th>
              <th>Type</th>
              <th>S</th>
              <th>AP</th>
              <th>D</th>
              <th>Abilities</th>
            </tr>

            {weaponsArr.map(function ([weapon, stats], index) {
              return (
                <tr>
                  <td>{weapon}</td>
                  <td>{stats.range}</td>
                  <td>{stats.type}</td>
                  <td>{stats.s}</td>
                  <td>{stats.ap}</td>
                  <td>{stats.d}</td>
                  <td>{stats.abilities}</td>
                </tr>
              );
            })}
          </table>
        </section>

        <section className={rules ? "campaign-info" : "campaign-info hidden"}>
          <StyledSubheader3>Rules</StyledSubheader3>
          <ul>
            {rulesArr.map(function ([key, value], index) {
              return (
                <li key={`rules-${index}`}>
                  <span><strong>{key}</strong>: {value}</span>
                </li>
              )
            })}
          </ul>
        </section>
      </section>
    </section>
  );
}

export default CharacterProfile;