import { useRef } from 'react';
import { StyledHeader, StyledSubheader, StyledSubheader3 } from '../styled';
import { majorFactions, minorFactions } from './factionData';
import './factions.css'

function Faction(props) {
  const {name, subhead, src, description} = props.faction;
  return (
    <section className="faction-section">
      <aside>
        <img src={src} alt={name}></img>
      </aside>
      <article>
        <StyledSubheader>{name}</StyledSubheader>
        <StyledSubheader3>{subhead}</StyledSubheader3>
        <p>{description}</p>
      </article>
    </section>
  );
}

function Factions() {
  const impRef = useRef(null);
  const xenosRef = useRef(null);
  return (
    <>
      <nav className="sub-nav">
        <ul className="navigation">
          <li onClick={() => impRef.current.scrollIntoView({ behavior: 'smooth' })}>Major Factions</li>
          <li onClick={() => xenosRef.current.scrollIntoView({ behavior: 'smooth' })}>Minor Factions</li>
        </ul>
      </nav>
      <StyledHeader ref={impRef}>Major Factions</StyledHeader>
      <ul>
        {majorFactions.map(function (faction, index) {
          return (
            <li key={`fact-imp-${index}`}>
              <Faction faction={faction} />
            </li>
          );
        })}
      </ul>

      <StyledHeader style={{paddingTop: '16px'}} ref={xenosRef}>Minor Factions</StyledHeader>
      <ul>
        {minorFactions.map(function (faction, index) {
          return (
            <li key={`fact-xen-${index}`}>
              <Faction faction={faction} />
            </li>
          );
        })}
      </ul>
    </>
  );
}

export default Factions;