import { StyledHeader } from './styled';
import aquila from '../assets/factions/imperium/aquila.svg';

function Home() {
  function leadingZeroes(num) {
    if (num < 10) {
      return `00${num}`;
    } else if (num < 100) {
      return `0${num}`;
    } else {
      return num;
    }
  }

  function dateConverter(ms) {
    const msInYear = 31557600000;
    const msToM3 = msInYear * 30;
    const msSinceM3 = ms - msToM3;
    const years = Math.floor(msSinceM3 / msInYear);
    const remainder = msSinceM3 % msInYear;
    const sections = Math.floor(remainder / (msInYear / 1000));

    return `6 ${leadingZeroes(sections)} ${leadingZeroes(years)}.M42`;
  }

  return (
    <>
      <StyledHeader>The Caldraeus Campaign</StyledHeader>
      <p>
        The Caldraeus Cluster is a star cluster and region of space in the Maelstrom 
        Zone of the Ultima Segmentum. Most star systems in the cluster are under
        Imperial control, although as with the rest of the Maelstrom Zone, their
        distance and relative inaccessibility from the rest of the Imperium leaves
        them in constant danger of encroachment, raid, or conquest by the many rogue
        and xenos factions in the infamously lawless territory.
      </p>
      <p>
        In the 42nd Millennium, the Era Indomitus marks a new chapter in the history 
        of the Caldraeus Cluster; the Great Rift in the galaxy has left its systems
        more isolated than ever, and factions both external and internal struggle for
        superiority, influence, or simply survival in a reshaped and uncertain galaxy.
      </p>
      <p>
        According to local Adeptus Administratum scribes, the current date is 
        approximately <strong>{dateConverter(Date.now())}</strong>, not accounting 
        for any distortions from the Warp or other influences but accounting for 
        some drift since losing contact with the Segmentum Solar. This date can be 
        checked against the timeline contained herein to sense the distance or 
        proximity of the recorded events.
      </p>
      <img src={aquila} className="symbol" alt="" />
    </>
  );
}

export default Home;